.footer{
    padding-top: 60px;
    
    background-color: var(--firstColor);
    &_wrap{
        display: grid;
        grid-template-columns: 1fr 1.5fr 1fr ;
        padding-bottom: 148px;
    }
    &__logo{
        margin: 0 auto;
        position: static;
        z-index: 1;
    }
}
.footer-cards_wrap{
    display: grid;
    grid-template-columns: repeat(3,1fr);
}
.footer-cards{
    
    display: flex;
    flex-flow: column nowrap;
    gap: 5px;
    
}

.footer-link, .footer__p{
    text-decoration: none;
    font-family: 'Circular',sans-serif;
font-weight: 450;
font-size: 14px;
color: var(--secondColor);
opacity: 0.7;
padding: 8px;


}

.footer__logo-p{
    padding: 0 auto;
    color: var(--mainColor);
}
.footer__p{
    max-width: 80%;
    line-height: 18px;
}
.footer-link:hover{
    color: var(--mainColor);
}
.footer__h4{
font-family: 'Circular';
font-weight: 500;
font-size: 16px;
color: var(--mainColor);
opacity: 0.85;
margin: 10px;
}


.footer__store-icon{
    padding: 10px 0px;
    
    display: flex;
    flex-direction: row;
    gap: 10px;
}
.footer__store-option, .footer__store-select{
    max-width: 50%;
    background-color: var(--firstColor);
    color: var(--secondColor);
}

.footer__copyright{
    background-color: gray;
   
    margin: 0 auto;
    padding: 10px;
}
@media screen and (max-width:999px){
   .footer_wrap{
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows:  1fr 1.5fr 1fr;
        
}

.footer-cards{
   
    flex-flow: column nowrap;
    gap: 10px;
  
   
}
.footer-link, .footer__p{
    padding: 20px 10px;
}
.footer__h4,.footer__p,.footer-link{
    max-width: 100%;
    margin: 10px auto;
}
.footer__store-select{
    margin: 10px 0px;
    max-height: 10%;
    max-width: 20%;
    
}
.footer__store-icon{
    padding: 10px 0px;
    max-height: 10%;
    display: flex;
    flex-direction: row;
    gap: 20px;
}
.footer__store-select{
    margin: 10px auto;
    padding: 5px;
}
}
@media screen and (max-width:770px){
    
    .footer__p{
       max-width: 80%;
       padding: 0;
    }
    .footer_wrap{
        padding-bottom: 28px;
    }
  }
  @media screen and (max-width:580px){
    .footer_wrap{
        grid-gap:10px;
    }
    .footer-cards{
           gap: 5px;
    }
    
    .footer__store-icon{
       
        gap:10px;
    } 
    .footer__h4,.footer-link{
        max-width: 30%;
        max-height: 30%;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
    .footer__store-select{
        max-width: 100%;
    }
  } 
  
 .icon,.icon-nobg{
    stroke: var(--mainColor);
 }
 a:hover .icon{
    fill:var(--mainColor)
 }
 a:hover .icon-nobg{
    stroke: var(--firstColor);
    fill:var(--firstColor);
 }
